import {
  FETCH_COURSES_SUCCESS,
  FETCH_BOOK_QUESTIONS_SUCCESS,
  FETCH_HOMEWORK_LIST_SUCCESS,
  FETCH_HOMEWORK_PREVIEW_SUCCESS,
  UPDATE_NAVPATH,
  SET_FILTER_PARAMS,
} from '../constants'

const initialState = {
  courses: null,
  questions: null,
};

export default function filter(state = initialState, action = {}) {
  switch (action.type) {
    case FETCH_COURSES_SUCCESS:
      return {
        ...state,
        courses: action.payload.data.list,
      }
    case FETCH_HOMEWORK_LIST_SUCCESS:
      return {
        ...state,
        homeworks: action.payload.data.list,
      }
    case FETCH_BOOK_QUESTIONS_SUCCESS:
      return {
        ...state,
        questions: action.payload.data,
      }
    case FETCH_HOMEWORK_PREVIEW_SUCCESS:
      return {
        ...state,
        preview: action.payload.data,
      }
    case UPDATE_NAVPATH:
      return {
        ...state,
        preview: {},
      }
    case SET_FILTER_PARAMS:
      return {
        ...state,
        filterParams: action.obj,
      }
    default:
      return state;
  }
}



