import {
  getHomework,
  correctHomework,
  saveHomework,
  getQuizPreview,
  postQuizCorrect,
} from './server'
import {
  FETCH_QUESTION,
  SAVE_HOMEWORK,
  SUBMIT_CORRECT,
  FETCH_QUIZ_PREVIEW,
  SAVE_QUIZ_CORRECT,
} from '../constants'

export { filterFetchHomeworkPreview } from './filter'
export { fetchQuizzesForStudent } from './quiz'


export function fetchQuestions(queryString) {
  return {
    type: FETCH_QUESTION,
    payload: {
      promise: getHomework(queryString).then((res) => res.data),
    },
  }
}

export function submitCorrect(data, cb) {
  const submitFunc = data.homeWork.type === 'quiz' ? postQuizCorrect : correctHomework
  return {
    type: SUBMIT_CORRECT,
    payload: {
      promise: submitFunc(data).then((res) => {
        if (res.data.code !== 0) {
          alert(res.data.description) //eslint-disable-line
        }
        cb()
        return {
          ...res.data,
          inx: data.index,
        }
      }),
    },
  }
}

export function saveHomeworkCorrect(data) {
  return {
    type: SAVE_HOMEWORK,
    payload: {
      promise: saveHomework(data).then(res => res.data),
    },
  }
}

export function fetchQuizPreview(data) {
  return {
    type: FETCH_QUIZ_PREVIEW,
    payload: {
      promise: getQuizPreview(data).then(res => res.data),
    },
  }
}

export function saveQuizCorrect(data) {
  return {
    type: SAVE_QUIZ_CORRECT,
    payload: {
      promise: postQuizCorrect(data).then(res => res.data),
    },
  }
}



