import React from 'react'
import PropTypes from 'prop-types'
import { Row, Button, Alert } from 'antd'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom'
import querySrting from 'query-string'
import QuesItem from '../../components/HWCQuestionItem'
import CorrectPanel from '../../components/CorrectPanel'
import * as actions from '../../actions/questions'

import s from './index.scss'

class HWCQuestions extends React.Component {
  constructor() {
    super()
    this.goBackOrNextQuestion = this.goBackOrNextQuestion.bind(this)
    this.save = this.save.bind(this)
    this.renderSummary = this.renderSummary.bind(this)
    this.next = this.next.bind(this)
    this.getTitle = this.getTitle.bind(this)
  }

  componentDidMount() {
    const { location, fetchQuestions, filterFetchHomeworkPreview } = this.props
    this.querystring = location.search
    fetchQuestions(this.querystring)
    this.querystringData = querySrting.parse(this.querystring)
    const data = this.querystringData
    data.pageNumber = parseInt(data.pageNumber, 10) || 1
    if (parseInt(data.inx, 10) === 10) {
      data.pageNumber += 1
    }
    delete data.studentId
    filterFetchHomeworkPreview(data);
  }

  componentWillReceiveProps(nextProps) {
    const { location, fetchQuestions } = this.props
    const { location: nextLocation } = nextProps

    this.querystring = nextLocation.search
    if (nextLocation.search && location.search !== nextLocation.search) {
      fetchQuestions(nextLocation.search)
    }
  }

  getTitle(question) {
    const { title, type } = question;
    if (type !== 'completion') {
      return title
    }
    const reg = /\?_(\d+)_\?/g
    return title.replace(reg, ($0, $1) => `<img src="${question.imgAnswers[$1 - 1]}" alt="" style="border-bottom: 1px solid #ccc"/>`)
  }

  save() {
    const { questions, saveHomeworkCorrect } = this.props
    saveHomeworkCorrect({
      homeWork: questions.homeWork,
      userId: questions.userId,
    })
  }

  next(url) {
    const { history, questions } = this.props
    const inx = parseInt(this.querystringData.inx, 10)
    if (inx + (this.querystringData.pageNumber * 10) >= questions.preview.total) {
      alert('已经是最后一份作业')
      return false
    }
    if (inx === 10) {
      this.querystringData.inx = 1
    } else {
      this.querystringData.inx = inx + 1;
    }
    this.querystringData.studentId = questions.preview.list[this.querystringData.inx - 1].userId

    history.push({
      pathname: url || '/homework/correct/questions',
      search: querySrting.stringify(this.querystringData),
    })
  }

  goBackOrNextQuestion(isGoNext) {
    const {
      match, questions, history,
    } = this.props
    let urlPath
    const curId = parseInt(match.params.curId, 10) + 1
    if (isGoNext && curId <= questions.manualQuestions.length) {
      urlPath = match.url.replace(/\d+(\/remark)?$/, curId)
      history.replace({
        pathname: urlPath,
        search: this.querystring,
      })
    } else {
      urlPath = match.url.replace(/\d+(\/remark)?$/, '1')
      this.next(urlPath)
    }
  }

  renderSummary() {
    const { questions, history } = this.props
    return (
      <Row type="flex" align="middle" justify="space-between" className={s.title}>
        <div className={s.summary}>
          <h4>学生：{questions.userId}</h4>
          <Row>
            <span>提交时间：{new Date(questions.submitTime).toLocaleString()}</span>
            <span>自动批改题目：{questions.autoQuestions.length}</span>
          </Row>
        </div>
        <Button type="保存并退出" size="large" onClick={() => history.goBack()}>返回到筛选页面</Button>
      </Row>

    )
  }

  renderCorrectPanel() {
    const {
      match, questions, submitCorrect, history,
    } = this.props
    const curQuestion = questions.manualQuestions[match.params.curId - 1]
    const questionId = curQuestion && curQuestion.questionId
    if (match.params.curId && questions.manualQuestions.length !== 0 && curQuestion && questionId) {
      const question = {
        ...curQuestion,
        title: questions.questionMap[questionId].content.description,
        type: questions.questionMap[questionId].pattern.desc,
        answer: questions.questionMap[questionId].answer,
      }
      return (
        <CorrectPanel
          back={() => this.goBackOrNextQuestion()}
          next={() => this.goBackOrNextQuestion(true)}
          history={history}
          submit={(score, image, cb) => submitCorrect({
            index: match.params.curId,
            homeWork: questions.homeWork,
            userId: questions.userId,
            questionId,
            score,
            mark: {
              images: [image],
            },
          }, cb)}
          question={question}
          score={match.params.status === 'remark' ? curQuestion.score : 0}
          imageUrl={match.params.status === 'remark' ? curQuestion.mark.images[0] : curQuestion.imgAnswers[0]}
        />
      )
    }
    return null
  }

  renderWarning() {
    const {
      history, questions,
    } = this.props

    return (
      <div className={s.box}>
        <Alert message={questions.description} type="warning" showIcon />
        <Row type="flex" justify="center" align="middle">
          <Button
            type="primary"
            className={s.btn}
            onClick={() => {
              this.next()
            }}
          >进入下一份
          </Button>
          <Button
            type="primary"
            size="large"
            className={s.btn}
            onClick={() => {
            history.goBack()
          }}
          >
            退出
          </Button>
        </Row>
      </div>
    )
  }

  render() {
    const {
      match, history, questions, fetchQuestions,
    } = this.props
    if (questions.code !== 0 && questions.description) {
      return this.renderWarning()
    }
    return (
      <div>
        {this.renderSummary()}
        {this.renderCorrectPanel()}
        <div className={s.box}>
          { questions.manualQuestions.map(question => {
            question = {
              ...question,
              title: questions.questionMap[question.questionId].content.description,
              type: questions.questionMap[question.questionId].pattern.desc,
            }
            return (
              <QuesItem
                key={question.questionId}
                urlPrefix={match.url}
                querystring={this.querystring}
                history={history}
                title={this.getTitle(question)}
                question={question}
              />
            )
          }) }
        </div>
        <Row type="flex" justify="center" align="middle">
          <Button
            type="primary"
            className={s.btn}
            onClick={() => {
              this.save()
              this.next()
          }}
          >保存并进入下一份
          </Button>
          <Button
            type="primary"
            size="large"
            className={s.btn}
            onClick={() => {
            this.save();
            history.goBack()
          }}
          >
            保存并退出
          </Button>
        </Row>
      </div>
    )
  }
}


HWCQuestions.propTypes = {
  questions: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  submitCorrect: PropTypes.func.isRequired,
  fetchQuestions: PropTypes.func.isRequired,
  saveHomeworkCorrect: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  questions: state.questions,
});

function mapDispatchToProps(dispatch) {
  return bindActionCreators(actions, dispatch);
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(HWCQuestions));



