import React from 'react'
import PropTypes from 'prop-types'
import { Button } from 'antd'
import s from './index.css'

const ButtonGroup = Button.Group
const constraints = { audio: true } //  多媒体配置项
let chunks = [] // 缓存录音blob片段
let mediaRecorder = null // MediaRecorder实例

// 初始化麦克风
const setupMediaDevice = () => {
  /* eslint-disable */
  if (navigator.mediaDevices.getUserMedia) {
    const onSuccess = (stream) => {
      mediaRecorder = new MediaRecorder(stream)
      mediaRecorder.start()
      mediaRecorder.onpause = (e) => {
        /*
         *暂停录音时并不会触发ondataavailable事件，
         *需调用requestData方法来触发ondataavailable向chunks写数据
        */
        mediaRecorder.requestData()
      }

      mediaRecorder.ondataavailable = (e) => {
        chunks.push(e.data)
        console.info('ondataavailable', chunks)
      }
    }
    const onError = (err) => {
      console.error(`The following error occured:${err.id}`)
      if (err.id === 'PermissionDeniedError') {
        alert('麦克风被禁止，请开启麦克风并重新刷新页面')
      } else if (err.id === 'NotFoundError') {
        alert('未找到麦克风设备，请确认并重新刷新页面')
      } else {
        alert('麦克风设备异常，请确认并重新刷新页面')
      }
    }
    navigator.mediaDevices.getUserMedia(constraints).then(onSuccess, onError)
  } else {
    console.error('getUserMedia not supported on your browser!')
  }
}

// 未录音按钮生成对应状态的icon和文字描述
const getOralConf = (oralState) => {
  let oralIcon = ''
  let oralHint = ''
  switch (oralState) {
    case 1:
      oralIcon = 'loading'
      oralHint = '录音中...'
      break;
    case 2:
      oralIcon = ''
      oralHint = '继续录音'
      break;
    case 3:
      oralIcon = ''
      oralHint = '重新录音'
      break;
    default:
      oralIcon = ''
      oralHint = '录音'
      break;
  }
  return { oralIcon, oralHint }
}
/**
 * 录音组件：包含试听、录音、删除、保存按钮
 * 
 * @export
 * @class OralCorrect
 * @extends {React.Component}
 */
export default class OralCorrect extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      oralState: 0, // 0:未录音  1: 录音中  2:录音暂停  3:重新录音
      listenTest: false, // 是否处于试听状态
      recordTime: 0,
    }
    this.soundHandler = this.soundHandler.bind(this)
    this.recordHandler = this.recordHandler.bind(this)
    this.deleteHandler = this.deleteHandler.bind(this)
    this.saveHandler = this.saveHandler.bind(this)
    this.setOralStatus = this.setOralStatus.bind(this)
    this.audition = document.createElement('audio') // 为试听功能生成一个audio
    this.timeout = null
  }

  componentDidMount() {
    this.audition.onended = () => {
      this.setState({ listenTest: false })
    }
    this.audition.onpause = () => {
      this.setState({ listenTest: false })
    }
  }

  componentWillUnmount() {
    this.audition = null
    this.existAudio = null
    mediaRecorder = null
    chunks = null
    clearInterval(this.timeout)
    this.timeout = null
  }

  // 听当前录音片段
  soundHandler() {
    const { listenTest } = this.state
    if (listenTest) {
      this.audition.pause()
      this.setState({ listenTest: false })
    } else if (chunks.length > 0) {
      const blob = new Blob(chunks, { 'type' : 'audio/mp3; codecs=opus' })
      const audioURL = window.URL.createObjectURL(blob)
      this.audition.src = audioURL
      this.audition.play()
      this.setState({ listenTest: true })
    }
  }

  // 删除当前录音片段
  deleteHandler() {
    const { saveOrDelAudio } = this.props 
    this.audition.pause() // 录音前暂停正在播放的试听
    if (mediaRecorder.state !== 'inactive') mediaRecorder.stop()
    this.setOralStatus(0)
    saveOrDelAudio(false)
  }

  // 保存当前录音
  saveHandler() {
    const { saveOrDelAudio } = this.props 
    this.audition.pause() // 录音前暂停正在播放的试听
    if (mediaRecorder.state !== 'inactive') mediaRecorder.stop()
    this.setOralStatus(3)
    const blob = new Blob(chunks, { 'type' : 'audio/mp3' })
    // const audioURL = window.URL.createObjectURL(blob)
    const fr = new FileReader()
    fr.onload = function(e) {
      saveOrDelAudio(true, e.target.result)
    }
    fr.readAsDataURL(blob)
  }

  // 录音按钮点击处理
  recordHandler() {
    this.audition.pause() //录音前暂停正在播放的试听
    const { oralState } = this.state
    if (oralState === 0 || oralState === 3) { // 首次或重新开始录音状态
      this.setOralStatus(1)
      chunks = [] // 初始化前清空之前的录音缓存数据
      if (mediaRecorder) {
        mediaRecorder.start()
      } else setupMediaDevice()
    } else if (oralState === 1) { // 录音中状态
      mediaRecorder.pause()
      this.setOralStatus(2)
    } else {  // 录音暂停状态
      mediaRecorder.resume()
      this.setOralStatus(1)
    }
  }

  // 保存录音各种状态
  setOralStatus(oralstatus){
    this.setState({ oralState: oralstatus })
    // 保存录音状态到coorectPanelNew
    const { setRecording } = this.props 
    setRecording(oralstatus)
    this.countDown(oralstatus)
  }

  // 3分钟计时
  countDown = (oralstatus) => {  
    if (oralstatus !== 1) {
      clearInterval(this.timeout)
      if (oralstatus === 0 || oralstatus === 3) {
        this.setState({ recordTime: 0 })
      }
      return
    }
    this.timeout = setInterval(() => {
      // 录音最多3分钟
      if (this.state.recordTime >= 180) {
        this.saveHandler()
      }
      this.setState({
        recordTime: ++this.state.recordTime,
      })
    }, 1000) 
  }

  render() {
    const { oralState, listenTest, txtToast,recordTime,time } = this.state
    const { oralIcon, oralHint } = getOralConf(oralState)
    return (
      <ButtonGroup className={s.oralBtnGroup}>
        {
          oralState < 2 ? <Button icon="sound" onClick={this.soundHandler} disabled /> :
          <Button icon={listenTest ? "pause-circle-o" : "sound"} onClick={this.soundHandler} />
        }
        <Button icon={oralIcon} onClick={this.recordHandler}>{recordTime ? `${oralHint} ${recordTime}秒` : oralHint}</Button>
        <Button icon="close-circle" type="danger" onClick={this.deleteHandler} disabled={oralState === 0}/>
        <Button icon="check-circle-o" type="primary" onClick={this.saveHandler} disabled={oralState !== 2}>完成录音</Button>
      </ButtonGroup>
    )
  }
}

OralCorrect.propTypes = {
  saveOrDelAudio: PropTypes.func.isRequired,
  setRecording: PropTypes.func.isRequired,
}



