import React from 'react'
import PropTypes from 'prop-types'
import { withRouter, matchPath } from 'react-router'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Layout, Menu, Icon } from 'antd'
import { Link } from 'react-router-dom'
import { getAllMenu, updateNavPath } from '../../../actions/menu'
import s from './index.less'

const { SubMenu } = Menu

const defaultProps = {
  items: [],
}

const propTypes = {
  items: PropTypes.array,
  history: PropTypes.object.isRequired,
  getAllMenu: PropTypes.func.isRequired,
  updateNavPath: PropTypes.func.isRequired,
}

const { Sider } = Layout;

const isActive = (path, history) => matchPath(path, {
  path: history.location.pathname,
  exact: true,
  strict: false,
})

class Sidebar extends React.Component {
  state = {
    openKey: 'sub1',
    activeKey: 'menu101',
    collapsed: false,
    mode: 'inline',
  }
  componentDidMount() {
    this.props.getAllMenu()
  }

  componentWillReceiveProps(nextProps) {
    Array.isArray(nextProps.items) && nextProps.items.map(item => { // eslint-disable-line
      Array.isArray(item.child) && item.child.map((node) => { // eslint-disable-line
        if (node.url && isActive(node.url, this.props.history)) {
          this.menuClickHandle({
            key: `menu${node.key}`,
            keyPath: [`menu${node.key}`, `sub${item.key}`],
          })
        } else if (node.url && node.url === '/homework/correct/filter' && this.props.history.location.pathname.startsWith('/pick/homework')) {
          this.menuClickHandle({
            key: `menu${node.key}`,
            keyPath: [`menu${node.key}`, `sub${item.key}`],
          })
        }
      })
    });
  }

  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
      mode: !this.state.collapsed ? 'vertical' : 'inline',
    });
  }


  menuClickHandle = (item) => {
    this.setState({
      activeKey: item.key,
    })
    this.props.updateNavPath(item.keyPath, item.key)
  }

  render() {
    const { items, history } = this.props
    let { activeKey, openKey } = this.state

    const _menuProcess = (nodes, pkey) => Array.isArray(nodes) && nodes.map((item) => {
      const menu = _menuProcess(item.child, item.key);
      if (item.url && isActive(item.url, history)) {
        activeKey = `menu${item.key}`
        openKey = `sub${pkey}`
      }
      if (menu.length > 0) {
        return (
          <SubMenu
            key={`sub${item.key}`}
            title={<span><Icon type={item.icon} /><span className="nav-text">{item.id}</span></span>}
          >
            {menu}
          </SubMenu>
        )
      }
      return (
        <Menu.Item key={`menu${item.key}`}>
          {
            item.url ?
              <Link to={item.url}>{item.icon && <Icon type={item.icon} />}{item.id}</Link> : // eslint-disable-line
              <span>{item.icon && <Icon type={item.icon} />}{item.id}</span>
          }
        </Menu.Item>
      )
    })

    const menu = _menuProcess(items);

    return (
      <Sider
        trigger={null}
        collapsible
        collapsed={this.state.collapsed}
        onCollapse={this.onCollapse}
      >
        <div className={s['ant-layout-logo']} />
        <Menu
          mode={this.state.mode}
          theme="dark"
          selectedKeys={[activeKey]}
          defaultOpenKeys={[openKey]}
          onClick={this.menuClickHandle}
        >
          {menu}
        </Menu>
        <div className={s['sider-trigger']}>
          <Icon
            className="trigger"
            type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
            onClick={this.toggle}
          />
        </div>
      </Sider>
    )
  }
}

Sidebar.propTypes = propTypes;
Sidebar.defaultProps = defaultProps;

function mapStateToProps(state) {
  return {
    items: state.menu.items,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    getAllMenu: bindActionCreators(getAllMenu, dispatch),
    updateNavPath: bindActionCreators(updateNavPath, dispatch),
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Sidebar))



