import React from 'react'
import PropTypes from 'prop-types'
import autoBind from 'react-autobind';
import { Input, Button, Icon, Table, Form } from 'antd'
import { withRouter } from 'react-router-dom'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import queryString from 'query-string'
import s from './index.scss'

import * as quizActions from '../../actions/quiz'

const FormItem = Form.Item;
const Search = Input.Search;
const dateFormat = require('dateformat');

class HWCQuizzes extends React.Component {
  constructor(props) {
    super(props)
    // 参数 type, id, start, end, index, studentId
    this.state = {
      searchParams: {
        keyword: '',
        page: 1,
        size: 10,
      },
      pagination: {
        showQuickJumper: true,
        current: 1,
        pageSize: 10,
        total: 10,
      },
    }
    autoBind(this)
  }

  componentDidMount() {
    const { match } = this.props
    this.initData(match.params, this.props)
  }

  componentWillReceiveProps(nextProps) {
    const { match, location } = this.props
    const {
      match: nextMatch, quizzes, studentQuizzes, type, location: nextLocation,
    } = nextProps
    const searchData = queryString.parse(nextLocation.search)
    const total = type === 'all' ? quizzes.total : studentQuizzes.total

    this.setState({
      pagination: {
        ...this.state.pagination,
        total,
        current: parseInt(searchData.page, 10) || 1,
      },
    })
    if (match.params.id !== nextMatch.params.id || location.search !== nextLocation.search) {
      this.initData(nextMatch.params, nextProps, searchData.page)
    }
  }

  onChangeSearch(event) {
    this.setState({
      searchParams: {
        ...this.state.searchParams,
        keyword: event.target.value,
      },
    })
  }

  // params: id,lessonId
  initData(params, props) {
    const { location, fetchQuizzes, fetchQuizzesForStudent } = props
    const data = queryString.parse(location.search)
    const searchParams = {
      ...this.state.searchParams,
      keyword: data.search,
      page: data.page || 1,
    }
    this.setState({
      searchParams,
    })
    if (params.id) {
      delete searchParams.keyword
      searchParams.quizId = params.id
      searchParams.lessonId = params.lessonId
      fetchQuizzesForStudent(searchParams)
    } else {
      fetchQuizzes(searchParams)
    }
  }

  historyPush(params) {
    const { history, location } = this.props
    const searchParams = queryString.parse(location.search)

    history.push({
      search: queryString.stringify({ ...searchParams, ...params }),
    })
  }

  handleTableChange(pagination) {
    this.historyPush({ page: pagination.current })
  }

  searchHandle(searchVal) {
    this.historyPush({ search: searchVal, page: 1 })
  }


  goStudentQuiz(quizId, lessonId) {
    const { history } = this.props
    history.push({
      pathname: `/homework/quiz/list/${quizId}/${lessonId}`,
    })
  }

  readAll({ quizId, lessonId }, inx) {
    const { fetchQuizzesReadAll } = this.props
    fetchQuizzesReadAll({ quizId, lessonId }, inx)
  }

  goPick(item, inx) {
    const { history } = this.props
    const { pagination } = this.state
    const pickParams = {
      quizId: item.quizId,
      lessonId: item.lessonId,
      userId: item.userId,
      username: item.username, // 批改页面所需信息
      submitTime: item.submitTime, // 批改页面所需信息
      page: pagination.current, // 获取列表页
      inx, // 获取列表页
    }
    history.push({
      pathname: '/pick/quiz/0',
      search: queryString.stringify(pickParams),
    })
  }

  renderSearch() {
    const { type } = this.props
    if (type !== 'all') {
      return null
    }
    const { keyword } = this.state.searchParams
    const suffix = keyword ? <Icon type="close-circle" onClick={() => this.searchHandle('')} /> : null;
    return (
      <div className={s.top}>
        <Search
          defaultValue={keyword}
          value={keyword}
          placeholder="搜索测试名称或ID"
          style={{ width: 200 }}
          onChange={this.onChangeSearch}
          onSearch={value => this.searchHandle(value)}
          suffix={suffix}
        />
      </div>
    )
  }

  renderBtn(text, item, inx) {
    return (
      <div>
        <Button size="small" type="primary" onClick={() => this.goStudentQuiz(item.quizId, item.lessonId)}>查看</Button>
        <br />
        <Button
          size="small"
          className={s.mt5}
          disabled={item.correctState === '全批完'}
          onClick={() => this.readAll(item, inx)}
        >已阅
        </Button>
      </div>
    )
  }

  renderBtnPick(text, item, inx) {
    return (
      <div>
        <Button
          size="small"
          onClick={() => this.goPick(item, inx)}
          disabled={item.code === 0}
        >批改
        </Button>
      </div>
    )
  }

  renderQuiz() {
    const { quizzes } = this.props
    if (!quizzes.total || quizzes.total === 0) return null
    const columns = [{
      title: '测试ID',
      dataIndex: 'quizId',
      key: 'quizId',
    }, {
      title: '课程ID',
      dataIndex: 'lessonId',
      key: 'lessonId',
    }, {
      title: '测试名称',
      dataIndex: 'title',
      key: 'title',
    }, {
      title: '测试类型',
      dataIndex: 'pattern',
      key: 'pattern',
    }, {
      title: '测试创建时间',
      dataIndex: 'createTime',
      key: 'createTime',
      render: time => <p>{dateFormat(new Date(time), 'yyyy/mm/dd, HH:MM:ss')}</p>,
    }, {
      title: '测试提交数量',
      dataIndex: 'submitNumber',
      key: 'submitNumber',
    }, {
      title: '测试批改状态',
      dataIndex: 'correctState',
      key: 'correctState',
    }, {
      title: '操作',
      render: this.renderBtn,
    }];
    return (
      <div className={s.result}>
        <div className={s.topBar}>
          最近测试
          <span>共<strong> {quizzes.total} </strong>份作业</span>
        </div>
        <Table
          columns={columns}
          rowKey={record => record.id}
          dataSource={quizzes.list}
          pagination={this.state.pagination}
          onChange={this.handleTableChange}
        />
      </div>
    )
  }

  renderStudentQuiz() {
    const { studentQuizzes } = this.props
    if (!studentQuizzes) return null
    const columns = [{
      title: '学员ID',
      dataIndex: 'userId',
      key: 'userId',
    }, {
      title: '用户昵称',
      dataIndex: 'username',
      key: 'username',
    }, {
      title: '提交时间',
      dataIndex: 'submitTime',
      key: 'submitTime',
      render: time => <p>{dateFormat(new Date(time), 'yyyy/mm/dd, HH:MM:ss')}</p>,
    }, {
      title: '批改状态',
      dataIndex: 'correctState',
      key: 'correctState',
    }, {
      title: '批改人',
      dataIndex: 'editor',
      key: 'editor',
    }, {
      title: '操作',
      render: this.renderBtnPick,
    }];
    return (
      <div className={s.result}>
        <div className={s.topBar}>
          <span>本次测试共<strong> {studentQuizzes.total} </strong>份</span>
        </div>
        <Table
          columns={columns}
          rowKey={record => record.id}
          dataSource={studentQuizzes.list}
          pagination={this.state.pagination}
          onChange={this.handleTableChange}
        />
      </div>
    )
  }

  render() {
    const { type } = this.props
    return (
      <div className={s.container}>
        {this.renderSearch()}
        {type === 'all' ? this.renderQuiz() : this.renderStudentQuiz()}
      </div>
    )
  }
}

HWCQuizzes.propTypes = {
  location: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  fetchQuizzes: PropTypes.func.isRequired,
  quizzes: PropTypes.object.isRequired,
  studentQuizzes: PropTypes.object.isRequired,
  type: PropTypes.string,
  match: PropTypes.object.isRequired,
  fetchQuizzesReadAll: PropTypes.func.isRequired,
}

HWCQuizzes.defaultProps = {
  type: 'all',
}

const mapStateToProps = (state) => {
  const { quizzes, studentQuizzes, type } = state.quiz
  return {
    quizzes,
    studentQuizzes,
    type,
  }
};

function mapDispatchToProps(dispatch) {
  return bindActionCreators(quizActions, dispatch)
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(HWCQuizzes));



