import { getHomework, correctHomework, saveHomework } from './server'
import {
  FETCH_QUESTION,
  SAVE_HOMEWORK,
  SUBMIT_CORRECT,
} from '../constants'

export { filterFetchHomeworkPreview } from './filter'


export function fetchQuestions(queryString) {
  return {
    type: FETCH_QUESTION,
    payload: {
      promise: getHomework(queryString).then((res) => res.data),
    },
  }
}

export function submitCorrect(data, cb) {
  return {
    type: SUBMIT_CORRECT,
    payload: {
      promise: correctHomework({
        homeWork: data.homeWork,
        userId: data.userId,
        questionId: data.questionId,
        score: data.score,
        mark: data.mark,
      }).then((res) => {
        if (res.data.code !== 0) {
          alert(res.data.description) //eslint-disable-line
        }
        return {
          ...res.data,
          inx: data.index,
        }
      }).then(cb),
    },
  }
}

export function saveHomeworkCorrect(data) {
  return {
    type: SAVE_HOMEWORK,
    payload: {
      promise: saveHomework(data).then(res => res.data),
    },
  }
}



