import React from 'react'
import PropTypes from 'prop-types'
import autoBind from 'react-autobind';
import { Row, Select, Input, Button, InputNumber, Table, Form } from 'antd'
import { withRouter, Link } from 'react-router-dom'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import queryString from 'query-string'
import s from './index.scss'
import {
  filterFetchCourses,
  filterFetchHomeworkList,
  filterFetchBookQuestions,
  filterFetchHomeworkPreview,
} from '../../actions/filter'

const dateFormat = require('dateformat');

class HWCFilter extends React.Component {
  constructor(props) {
    super(props)
    // 参数 type, id, start, end, index, studentId
    this.state = {
      filterParams: this.props.filterParams || {},
      pagination: {
        showQuickJumper: true,
        current: 1,
        pageSize: 10,
        total: 100,
      },
    }
    autoBind(this)
  }

  componentDidMount() {
    const {
      location, preview, FetchHomeworkList, FetchHomeworkPreview, filterParams,
    } = this.props
    this.props.FetchCourses()
    // 从pick页面点击面包屑跳回作业批改的时候，显示要查询的作业列表
    if (filterParams) {
      const data = filterParams
      if (data.course) {
        FetchHomeworkList(data.course)
        FetchHomeworkPreview(data)
      }
      this.state.pagination.current = parseInt(data.pageNumber, 10) || 1
      this.setState({
        ...this.state,
        filterParams: data,
      })
    }
    // 下面的代码使用场景没搞懂？
    if (location.search && preview) {
      const data = queryString.parse(location.search)
      if (data.course) {
        FetchHomeworkList(data.course)
        FetchHomeworkPreview(data)
      }
      this.state.pagination.current = parseInt(data.pageNumber, 10) || 1
      this.setState({
        ...this.state,
        filterParams: data,
      })
    }
  }

  componentWillReceiveProps(nextProps, nextState) {
    this.setState({ pagination: { ...nextState.pagination, total: nextProps.preview.total } });
  }

  onCourseSelectChange(value) {
    const { FetchHomeworkList } = this.props;
    this.updateParams('course', value)
    FetchHomeworkList(value)
  }

  updateParams(key, value) {
    const {
      history, FetchBookQuestions, FetchHomeworkPreview,
    } = this.props
    let { filterParams } = this.state
    let updateQuestion = false
    if (key === 'bookType') {
      const tr = value.match(/^(\w+)-/)
      const idr = value.match(/-(\d+)$/)
      if (!tr || !idr) {
        return console.error('获取到的练习册类型和 id错误')
      }
      filterParams = {
        ...filterParams,
        type: tr[1],
        id: idr[1],
      }
      updateQuestion = true
    } else if (key === 'index') {
      const arr = value.split(':')
      filterParams = {
        ...filterParams,
        index: arr[0],
        questionId: arr[1],
      }
      FetchHomeworkPreview(filterParams)
    } else {
      if (key === 'end') {
        updateQuestion = true
      }
      filterParams[`${key}`] = value
    }
    this.setState({
      filterParams,
    })
    if (updateQuestion && filterParams.end && filterParams.id) {
      FetchBookQuestions(filterParams.end, filterParams.id)
    }
    history.replace({
      search: queryString.stringify(filterParams),
    })
  }

  handleTableChange(pagination) {
    const { history } = this.props
    const newParams = {
      ...this.state.filterParams,
      pageNumber: pagination.current,
    };
    this.props.FetchHomeworkPreview(newParams)
    this.setState({ pagination, filterParams: newParams });
    history.replace({
      search: queryString.stringify(newParams),
    })
  }

  renderBookSelect() {
    const { courses, homeworks } = this.props
    const { filterParams } = this.state
    return (
      <Row type="flex" justify="start" align="middle">
        <label>选择练习册：</label>
        <Select
          placeholder="选择课程"
          className={s.select}
          value={filterParams.course}
          onChange={this.onCourseSelectChange}
        >
          {courses && courses.map((item) => (
            <Select.Option key={`${item.id}`}>
              {item.title}
            </Select.Option>
          ))
          }
        </Select>
        <Select
          placeholder="选择练习册"
          className={s.select}
          typeid={filterParams.type && filterParams.id ? `${filterParams.type}${filterParams.id}` : undefined}
          onChange={(typeid) => this.updateParams('bookType', typeid)}
        >
          {homeworks && homeworks.map((item) =>
            <Select.Option key={`${item.type}-${item.id}`}>{item.title}</Select.Option>)
          }
        </Select>
      </Row>
    )
  }

  renderPageInxSelect() {
    const { questions } = this.props
    const { filterParams } = this.state
    return (
      <Row type="flex" align="middle" className={s.mgt15}>
        <label>要批改的页码：</label>
        <InputNumber
          placeholder="起始页码"
          className={s.input}
          value={filterParams.start}
          onChange={(value) => this.updateParams('start', value)}
        />
        <span>&nbsp;页&nbsp;&nbsp;至&nbsp;</span>
        <InputNumber
          placeholder="结束页码"
          className={s.input}
          value={filterParams.end}
          onChange={(value) => this.updateParams('end', value)}
        />
        <span>&nbsp;页</span>
        <Select
          placeholder="截止到第几题"
          className={s.select}
          value={filterParams.index && filterParams.questionId ? `${filterParams.index}:${filterParams.questionId}` : undefined}
          onChange={(val) => this.updateParams('index', val)}
        >
          <Select.Option
            value="0:0"
            key={0}
          >
            {0}:{0}
          </Select.Option>
          {questions ? questions.map((question, idx) => (
            <Select.Option
              value={`${idx + 1}:${question.id}`}
              key={`${idx + 1}:${question.id}`}
            >
              {idx + 1}:{question.id}
            </Select.Option>
            )) : null
          }
        </Select>
      </Row>
    )
  }

  renderOp(text, record, inx) {
    return (
      <Button className={s.submitBtn}>
        <Link to={{ // eslint-disable-line
          pathname: '/pick/homework/0',
          search: queryString.stringify({
            ...this.state.filterParams,
            studentId: record.userId,
            inx,
          }),
        }}
          className={s.submitLink}
        >
          {record.manualQStatus.uncorrected === 0 ? '修改批改' : '批改'}
        </Link>
      </Button>
    );
  }

  renderResult() {
    const { preview } = this.props
    if (!preview) return null
    const columns = [{
      title: 'UserId',
      dataIndex: 'userId',
      key: 'userId',
    }, {
      title: '题目状态',
      dataIndex: 'status',
      key: 'status',
      render: status => <p>{status}</p>,
    }, {
      title: '自动批改',
      dataIndex: 'autoQStatus',
      key: 'autoQStatus',
      render: status => <p>{status.correct}|{status.wrong}|{status.miss}</p>,
    }, {
      title: '人工批改',
      dataIndex: 'manualQStatus',
      key: 'manualQStatus',
      render: status => <p>{status.corrected}|{status.uncorrected}|{status.avgScore}</p>,
    }, {
      title: '提交时间',
      dataIndex: 'submitTime',
      key: 'submitTime',
      render: time => <p>{dateFormat(new Date(time), 'yyyy/mm/dd, HH:MM:ss')}</p>,
    }, {
      title: '批改时间',
      dataIndex: 'correctTime',
      key: 'correctTime',
      render: time => <p>{dateFormat(new Date(time), 'yyyy/mm/dd, HH:MM:ss')}</p>,
    }, {
      title: '批改人',
      dataIndex: 'editor',
      key: 'editor',
    }, {
      title: '操作',
      render: this.renderOp,
    }];
    return (
      <div className={s.result}>
        <h4>您将批改{preview.title}</h4>
        <span>共{preview.total}份作业</span>
        <span>已经批改：{preview.corrected}份</span>
        <span>未批改：{preview.uncorrected}份</span>
        <Table
          columns={columns}
          rowKey={record => record.userId}
          dataSource={preview.list}
          pagination={this.state.pagination}
          onChange={this.handleTableChange}
        />
      </div>
    )
  }

  renderPreview() {
    const { FetchHomeworkPreview } = this.props
    const { filterParams } = this.state
    FetchHomeworkPreview(filterParams)
  }

  render() {
    const { preview } = this.props
    return (
      <div>
        <Row className={s.title}>请选择您本次要批改的作业</Row>
        <div className={s.box}>
          {this.renderBookSelect()}
          {this.renderPageInxSelect()}
          <Row type="flex" align="middle" className={s.mgt15}>
            <label>学生账号：</label>
            <Input
              placeholder="输入学生账号"
              onChange={e => this.updateParams('studentId', e.target.value)}
              className={`${s.input} ${s.mgl15}`}
            />
            <Button type="primary" onClick={this.renderPreview} className={s.mgl15}>查询</Button>
          </Row>
          {this.renderResult()}
          <Button type="primary" size="large" disabled={Object.keys(preview).length === 0} className={s.submitBtn}>
            <Link to={{ // eslint-disable-line
              pathname: '/pick/homework/0',
              search: queryString.stringify(this.state.filterParams),
            }}
              className={s.submitLink}
            >
              开始批改
            </Link>
          </Button>
        </div>
      </div>
    )
  }
}

HWCFilter.propTypes = {
  location: PropTypes.object.isRequired,
  courses: PropTypes.array,
  homeworks: PropTypes.array,
  questions: PropTypes.array,
  preview: PropTypes.object,
  history: PropTypes.object.isRequired,
  filterParams: PropTypes.object.isRequired,
  FetchCourses: PropTypes.func.isRequired,
  FetchHomeworkList: PropTypes.func.isRequired,
  FetchBookQuestions: PropTypes.func.isRequired,
  FetchHomeworkPreview: PropTypes.func.isRequired,
};
HWCFilter.defaultProps = {
  courses: [],
  homeworks: [],
  questions: [],
  preview: {},
}

const mapStateToProps = (state) => {
  const {
    courses, homeworks, questions, preview, filterParams,
  } = state.filter;
  return {
    courses,
    homeworks,
    questions,
    preview,
    filterParams,
  };
};

function mapDispatchToProps(dispatch) {
  return {
    FetchCourses: bindActionCreators(filterFetchCourses, dispatch),
    FetchHomeworkList: bindActionCreators(filterFetchHomeworkList, dispatch),
    FetchBookQuestions: bindActionCreators(filterFetchBookQuestions, dispatch),
    FetchHomeworkPreview: bindActionCreators(filterFetchHomeworkPreview, dispatch),
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form.create()(HWCFilter)));



