import React from 'react'
import PropTypes from 'prop-types'
import { Tabs } from 'antd'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom'
import queryString from 'query-string'
import QuestionItem from '../../components/HWCAnswers/QuestionItem'
import CorrectPanel from '../../components/CorrectPanelNew'
import * as actions from '../../actions/pick'
import { filterSetFilterParams } from '../../actions/filter'
import s from './index.scss'
import Toast from '../../components/global/Toast'

const TabPane = Tabs.TabPane

class HWPick extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      curPicInx: 0, // literallycanvas的序号
      txtToast: null,
    }
    this.changeInx = this.changeInx.bind(this)
    this.goBackOrNextQuestion = this.goBackOrNextQuestion.bind(this)
  }

  componentDidMount() {
    this.initData(this.props)
  }

  componentWillReceiveProps(nextProps) {
    const { match, location } = this.props
    const { match: nextMatch, location: nextLocation } = nextProps
    if (location.search !== nextLocation.search) {
      this.initData(nextProps)
    }
    if (match.params.curInx !== nextMatch.params.curInx) {
      this.setState({
        curPicInx: 0,
      })
    }
  }

  // 获取下一个未批改的题(测试批改包括已阅状态的题，不包括漏做的题)
  getNextInx = (curInx, detail) => {
    let nextInc = curInx + 1
    while (nextInc < detail.length) {
      if (detail[nextInc].score < (this.isHomeWork ? 0 : -1)) {
        return nextInc
      }
      nextInc += 1
    }
    return nextInc
  }

  // 设置10s内当前题目滚到列表最上方
  setScrollTop = (inx, tabKey) => {
    const currTab = this.refs[`currTab${tabKey}_${inx}`]
    if (currTab) {
      let scrollTopBefore = currTab.parentNode.scrollTop
      const scrollTopAfter = currTab.offsetTop - 50
      const stepTop = (scrollTopAfter - scrollTopBefore) / 10
      let intervalID = null
      const step = () => {
        const scrollTopStep = scrollTopBefore + stepTop;
        if (scrollTopStep < scrollTopAfter) {
          currTab.parentNode.scrollTop = scrollTopStep
          scrollTopBefore = scrollTopStep;
        } else {
          currTab.parentNode.scrollTop = scrollTopAfter
          clearInterval(intervalID);
        }
      }
      intervalID = setInterval(step, 10);
    }
  }

  changeTabs = (key) => {
    const { match } = this.props
    const curInx = parseInt(match.params.curInx, 10)
    setTimeout(() => this.setScrollTop(curInx, key), 100)
  }

  initData(props) {
    const {
      location, fetchQuizPreview, fetchQuestions, fetchQuizzesForStudent,
      filterFetchHomeworkPreview, match,
    } = props
    const type = match.params.type
    this.isHomeWork = type === 'homework'
    this.querystring = queryString.parse(location.search) || {}
    const {
      quizId, inx, pageNumber, lessonId,
    } = this.querystring
    let { page } = this.querystring
    const tmpPageNum = parseInt(pageNumber, 10) || 1
    if (parseInt(inx, 10) === 9) {
      page = parseInt(page, 10) + 1
      this.querystring.pageNumber = tmpPageNum + 1
    }
    if (type === 'quiz') {
      fetchQuizPreview(this.querystring)
      fetchQuizzesForStudent({
        quizId,
        page,
        lessonId,
      })
    } else if (type === 'homework') {
      const copyQuerystring = Object.assign({}, this.querystring)
      fetchQuestions(location.search)
      delete copyQuerystring.studentId
      filterFetchHomeworkPreview(copyQuerystring)
    }
  }

  changeInx(inx) {
    const { curPicInx } = this.state
    if (inx !== curPicInx) {
      this.setState({
        curPicInx: inx,
      })
    }
  }

  goBackOrNextQuestion() {
    const {
      match, history, detail, lists,
    } = this.props
    const { type } = match.params
    const curInx = parseInt(match.params.curInx, 10)
    const nextInc = this.getNextInx(curInx, detail)
    if (nextInc < detail.length) {
      history.replace({
        pathname: `/pick/${type}/${nextInc}`,
        search: queryString.stringify(this.querystring),
      })
      this.setScrollTop(nextInc, 0)
    } else if (nextInc === detail.length) {
      const inx = parseInt(this.querystring.inx, 10)
      const newInx = (inx + 1) % 9
      if (newInx === lists.total) {
        this.setState({
          txtToast: '全部批改完成',
        })
        if (!this.isHomeWork) {
          setTimeout(() => {
            history.push({
              pathname: this.isHomeWork ? `/homework/quiz/list/${detail[0].quizId}` : '/homework/quiz/list/',
            })
          }, 3000)
        }
        return;
      }
      if (this.isHomeWork) {
        this.querystring.studentId = lists.list[newInx].userId
      } else {
        this.querystring.userId = lists.list[newInx].userId
        delete this.querystring.username
      }
      this.setState({
        txtToast: '下一位学员',
      })
      history.push({
        pathname: `/pick/${type}/0`,
        search: queryString.stringify({
          ...this.querystring,
          inx: newInx,
        }),
      })
      this.setScrollTop(0, 0)
    }
  }

  renderTabs() {
    const tabs = [
      {
        tabName: '原题',
      },
      {
        tabName: '答案解析',
      },
    ]
    return (
      <Tabs defaultActiveKey="0" animated={false} onChange={this.changeTabs}>
        {tabs.map((tab, inx) => this.renderTabPane(tab.tabName, inx))}
      </Tabs>
    )
  }

  renderUserInfo() {
    if (!this.querystring) {
      return null
    }
    const { detail } = this.props
    const { userName, submitTime } = this.querystring
    if (detail.length === 0) return <div>数据获取失败</div>
    return (
      <div>
        <h3 className={s.title}>{userName || detail[0].userId}</h3>
        <div>{new Date(parseInt(submitTime || detail[0].submitTime, 10)).toLocaleString()} 提交</div>
        {this.isHomeWork ? <span>自动批改题目：{detail.length}</span> : null}
      </div>
    )
  }

  renderTabPane(tabName, tabKey) {
    const { detail, match, location } = this.props
    const curInx = parseInt(match.params.curInx, 10)
    return (
      <TabPane tab={tabName} key={tabKey} className={s.tabpane}>
        { detail.map((question, inx) => {
          question = {
            ...question,
            title: question.content,
          }
          const title = tabKey === 0 ? question.content : question.analyses
          return (
            <div
              ref={`currTab${tabKey}_${inx}`}
              onClick={() => this.setScrollTop(inx, tabKey)}
            >
              <QuestionItem
                isActive={curInx === inx}
                inx={inx}
                key={question.questionId}
                title={title}
                location={location}
                question={question}
                tabName={tabName}
                type={match.params.type}
              />
            </div>
          )
        }) }
      </TabPane>
    )
  }


  renderCorrectPanel() {
    const {
      match, detail, submitCorrect, history, SetFilterParams,
    } = this.props
    const { curInx } = match.params
    const { curPicInx } = this.state
    if (detail.length === 0) return <div>数据获取失败</div>
    const curDetail = detail[curInx]

    const { quizId, score } = curDetail
    const submitData = {
      ...curDetail,
      status: score !== -2 ? -1 : 0, // status = -1 时表示重新批改
      index: curInx, // 当前题目的下标，从0开始
      mark: {
        images: [], // 批改图片
        audio: '',
        text: '', // TODO：保留字段，以后有用
      },
    }
    if (!this.isHomeWork) {
      submitData.homeWork = {
        type: 'quiz',
        id: quizId, // quizId
      }
    }
    return (
      <CorrectPanel
        next={() => this.goBackOrNextQuestion()}
        SetFilterParams={SetFilterParams}
        history={history}
        curPicInx={curPicInx}
        curInx={+curInx}
        curUserInx={parseInt(queryString.parse(history.location.search).inx, 10)}
        changeInx={this.changeInx}
        submit={(submitScore, images, audio, cb) => {
          submitData.score = submitScore
          submitData.mark.images = images
          submitData.mark.audio = audio
          submitCorrect(submitData, cb)
          // fetchQuestions(location.search) // 在提交答案后重新获取一次问题列表
        }}
        question={curDetail}
        isHomeWork={this.isHomeWork}
      />
    )
  }

  render() {
    const { detail } = this.props
    const { txtToast } = this.state
    if (!detail) {
      return null
    }
    return (
      <div className={s.layout}>
        {this.renderUserInfo()}
        <div>
          <div className={s.left}>
            {this.renderTabs()}
          </div>
          <div className={s.right}>
            {this.renderCorrectPanel()}
          </div>
        </div>
        {txtToast ? <Toast txt={txtToast} /> : null}
      </div>
    )
  }
}

HWPick.propTypes = {
  // pickstatus: PropTypes.number.isRequired,
  match: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  lists: PropTypes.object.isRequired,
  detail: PropTypes.array.isRequired,
  submitCorrect: PropTypes.func.isRequired,
  SetFilterParams: PropTypes.func.isRequired,
}

const mapStateToProps = (state) => {
  const { detail, lists } = state.pick
  return {
    detail,
    lists,
  }
};

function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    ...actions,
    SetFilterParams: bindActionCreators(filterSetFilterParams, dispatch),
  }, dispatch);
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(HWPick));



