/* eslint-disable no-nested-ternary */
import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import { Row, Tag } from 'antd';
import { className } from '../../../utils/index'

import s from './index.scss'

export default class QuestionItem extends React.Component {
  componentDidMount() {
    if (window.MathJax) {
      window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub, 'root']);
    }
    // 处理左边框的问题
    const currTab = this.refs.currTabActive
    if (currTab) {
      currTab.parentNode.parentNode.scrollTop = currTab.offsetTop - 50
    }
  }

  render() {
    const {
      question, title, isActive, inx, location, tabName, type,
    } = this.props;
    return (
      <div ref={isActive ? 'currTabActive' : null}>
        <Link
          to={`/pick/${type}/${inx}${location.search}`}
          replace
          className={s.link}
        >
          <div
            className={className(s.item, { [s.itemActive]: isActive })}
          >
            <Row type="flex" align="middle" justify="space-between">
              <div>{question.questionId}.</div>
              {type === 'homework' ?
                question.score === -1 ? <Tag color="volcano">未批改</Tag> : <Tag color="cyan">已批改</Tag>
                :
                question.score === -3 ? <Tag color="purple">已阅</Tag> : question.score === -2 ? <Tag color="volcano">未批改</Tag> : question.score === -1 ? <Tag color="orange">漏做</Tag> : <Tag color="cyan">已批改</Tag>
              }
            </Row>
            <div dangerouslySetInnerHTML={{ __html: `${tabName}: ${title}` }} />
          </div>
        </Link>
      </div>
    )
  }
}

QuestionItem.propTypes = {
  question: PropTypes.object.isRequired,
  tabName: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  location: PropTypes.object.isRequired,
  isActive: PropTypes.bool.isRequired,
  inx: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
};



