import React from 'react'
import PropTypes from 'prop-types'
import { Button } from 'antd'
import s from './index.css'

const ButtonGroup = Button.Group

/**
 * 音频播放组件：包括音频播放、删除功能
 * @export
 * @class AudioPlayer
 * @extends {React.Component}
 */
export default class AudioPlayer extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      listening: false,
    }
    this.listenAudioHandler = this.listenAudioHandler.bind(this)
    this.deleteAudioHandler = this.deleteAudioHandler.bind(this)
    this.audio = document.createElement('audio')
  }

  componentDidMount() {
    const { audioSrc } = this.props
    if (audioSrc && audioSrc.length > 0) this.audio.src = audioSrc
    this.audio.onended = () => {
      this.setState({ listening: false })
    }
    this.audio.onpause = () => {
      this.setState({ listening: false })
    }
  }

  componentWillUnmount() {
    this.audio = null
  }

  listenAudioHandler() {
    const { listening } = this.state
    const { audioSrc } = this.props
    if (listening) {
      this.audio.pause()
      this.setState({ listening: false })
    } else if (audioSrc && audioSrc.length > 0) {
      this.audio.play()
      this.setState({ listening: true })
    }
  }

  deleteAudioHandler() {
    const { saveOrDelAudio } = this.props
    saveOrDelAudio(false)
  }

  render() {
    const { listening } = this.state
    return (
      <ButtonGroup className={s.audio}>
        <Button icon={listening ? 'pause-circle-o' : 'sound'} onClick={this.listenAudioHandler}>已点评录音</Button>
        <Button icon="close-circle" type="danger" onClick={this.deleteAudioHandler} />
      </ButtonGroup>
    )
  }
}

AudioPlayer.propTypes = {
  audioSrc: PropTypes.string.isRequired,
  saveOrDelAudio: PropTypes.func.isRequired,
}



