import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Home from '../containers/Home';
import HWHome from '../containers/HWHome';
import HWCHome from '../containers/HWCHome';
import HWCFilter from '../containers/HWCFilter';
import HWCQuestions from '../containers/HWCQuestions';
import HWQuizzes from '../containers/HWQuizzes';
import Pick from '../containers/Pick';
import HWAssign from '../containers/HWAssign';
import HWStats from '../containers/HWStats';

const routes = (
  <Switch>
    <Route path="/" exact component={Home} />
    <Route path="/homework/correct/filter" component={HWCFilter} />
    <Route path="/homework/correct/questions/:curId?/:status?" component={HWCQuestions} />
    <Route path="/homework/quiz/list/:id?/:lessonId?" component={HWQuizzes} />
    <Route path="/pick/:type/:curInx" component={Pick} />
    <Route path="/homework/correct/" component={HWCHome} />
    <Route path="/homework/assign" component={HWAssign} />
    <Route path="/homework/stats" component={HWStats} />
    <Route path="/homework" component={HWHome} />
  </Switch>
);

export default routes



