import {
  FETCH_QUIZ_PREVIEW_SUCCESS,
  SUBMIT_CORRECT_SUCCESS,
  FETCH_STUDENT_QUIZZES_SUCCESS,
  FETCH_QUESTION_SUCCESS,
  FETCH_HOMEWORK_PREVIEW_SUCCESS,
} from '../constants'

const initialState = {
  detail: null,
  studentQuizzes: [],
};

function homeworkToQuizStyle(homeWorkData) {
  if (!homeWorkData) {
    return []
  }
  const detail = []
  homeWorkData.manualQuestions.map((item, inx) => {
    const tmpQuestionMap = homeWorkData.questionMap[item.questionId]
    detail[inx] = {
      ...item,
      content: tmpQuestionMap.content.description,
      analyses: tmpQuestionMap.answer,
      imgCorrect: item.mark && item.mark.images,
      userId: homeWorkData.userId,
      submitTime: homeWorkData.submitTime,
      homeWork: homeWorkData.homeWork,
    }
    return item
  })
  return detail
}

export default function pick(state = initialState, action = {}) {
  switch (action.type) {
    case FETCH_QUIZ_PREVIEW_SUCCESS:
      return {
        ...state,
        detail: action.payload.data,
      }
    case FETCH_QUESTION_SUCCESS:
      return {
        ...state,
        detail: homeworkToQuizStyle(action.payload.data),
        code: action.payload.code,
        description: action.payload.description,
      }
    case FETCH_STUDENT_QUIZZES_SUCCESS:
      return {
        ...state,
        lists: action.payload.data,
      }
    case FETCH_HOMEWORK_PREVIEW_SUCCESS:
      return {
        ...state,
        lists: action.payload.data,
      }
    case SUBMIT_CORRECT_SUCCESS: {
      const tmpDetail = state.detail.map((item, inx) => {
        const tmpItem = { ...item }
        if (inx === parseInt(action.payload.inx, 10)) {
          tmpItem.score = action.payload.data.score
          tmpItem.imgCorrect = action.payload.data.mark.images
          if (!tmpItem.mark) tmpItem.mark = {}
          tmpItem.mark.audio = action.payload.data.mark.audio
          return tmpItem
        }
        return item
      })
      return {
        ...state,
        detail: tmpDetail,
        pickStatus: action.payload.data.status, // 0 未批改完、 1 已批改、 2 全部学生都已批改
      }
    }
    default:
      return state;
  }
}



