import {
  FETCH_QUIZZES_SUCCESS,
  FETCH_STUDENT_QUIZZES_SUCCESS,
  FETCH_QUIZZES_READ_ALL_SUCCESS,
} from '../constants';

const initialState = {
  quizzes: {},
  studentQuizzes: {},
};

export default function quiz(state = initialState, action = {}) {
  switch (action.type) {
    case FETCH_QUIZZES_SUCCESS:
      return {
        ...state,
        type: 'all',
        quizzes: action.payload.data,
      };
    case FETCH_STUDENT_QUIZZES_SUCCESS:
      return {
        ...state,
        type: 'student',
        studentQuizzes: action.payload.data,
      };
    case FETCH_QUIZZES_READ_ALL_SUCCESS: {
      const tmpList = state.quizzes.list.map((item, inx) => {
        const tmpItem = { ...item }
        if (inx === parseInt(action.payload.inx, 10)) {
          tmpItem.correctState = '全批完'
          return tmpItem
        }
        return item
      })
      return {
        ...state,
        quizzes: {
          ...state.quizzes,
          list: tmpList,
        },
      };
    }
    default:
      return state;
  }
}



