import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'
import { Row, Col, Button } from 'antd';
import Lightbox from 'react-image-lightbox';

import PanelBox from '../PanelBox'
import s from './index.css'

export default class HWCQuestionItem extends React.Component {
  constructor() {
    super()
    this.onAnswerClick = this.onAnswerClick.bind(this)
    this.renderEssayView = this.renderEssayView.bind(this)
    this.renderCompletionView = this.renderCompletionView.bind(this)

    this.state = {
      answerOpen: false,
    }
  }

  componentDidMount() {
    if (window.MathJax) {
      window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub, 'root']);
    }
  }
  onAnswerClick() {
    this.setState({
      answerOpen: true,
    })
  }

  renderEssayView() {
    const {
      question, querystring, history,
    } = this.props;
    const unCorrected = question.score === -1

    return (
      <div>
        <Row type="flex" justify="space-between">
          <Col span={11}>
            <p>他的回答</p>
            <div className={s.answerImg} onClick={this.onAnswerClick}>
              <img src={question.imgAnswers[0]} alt="" />
            </div>
            <Row type="flex" align="middle" justify="center">
              <Button type="primary">
                <Link to={{ // eslint-disable-line
                  pathname: `/homework/correct/questions/${question.index}`,
                  search: querystring,
                }}
                >{ question.score < 0 ? '批改' : '重新批改'}
                </Link>
              </Button>
            </Row>
          </Col>
          <div className={s.line} />
          <Col span={11}>
            <p>您的批改    <strong>{unCorrected ? null : `${question.score === -2 ? '未完成' : `得分:${question.score}`}`}</strong></p>
            <div
              className={s.answerImg}
              onClick={() => {
                if (unCorrected) return
                history.push({
                  pathname: `/homework/correct/questions/${question.index}/remark`,
                  search: querystring,
                })
              }}
            >
              {
                unCorrected ? (
                  '您还未批改'
                ) : <img src={question.mark.images[0]} alt="" />
              }
            </div>
          </Col>
        </Row>
        { this.state.answerOpen &&
        <Lightbox
          mainSrc={question.imgAnswers[0]}
          onCloseRequest={() => this.setState({ answerOpen: false })}
        />
        }
      </div>
    )
  }

  renderCompletionView() {
    const { question, querystring } = this.props;
    const unCorrected = question.score === -1

    return (
      <div>
        <p>您的批改    <strong>{unCorrected ? '未批改' : `${question.score === -2 ? '未完成' : `得分:${question.score}`}`}</strong></p>
        <Button type="primary" style={{ marginTop: '15px' }}>
          <Link to={{ // eslint-disable-line
            pathname: `/homework/correct/questions/${question.index}`,
            search: querystring,
          }}
          >{ question.score < 0 ? '批改' : '重新批改'}
          </Link>
        </Button>
      </div>
    )
  }

  render() {
    const { question, title } = this.props;
    const { type } = question;
    return (
      <PanelBox title={`${question.questionId}. ${title}`}>
        {type === 'completion' ? this.renderCompletionView() : this.renderEssayView()}
      </PanelBox>
    )
  }
}

HWCQuestionItem.propTypes = {
  question: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  querystring: PropTypes.string.isRequired,
};



