import {
  getMyCourses,
  getHomeworkList,
  getBookQuestionByPage,
  getHomeworkPreview,
} from './server'
import {
  FETCH_COURSES,
  FETCH_HOMEWORK_LIST,
  FETCH_BOOK_QUESTIONS,
  FETCH_HOMEWORK_PREVIEW,
  SET_FILTER_PARAMS,
} from '../constants'

export function filterFetchCourses() {
  return {
    type: FETCH_COURSES,
    payload: {
      promise: getMyCourses().then((res) => res.data),
    },
  }
}

export function filterFetchHomeworkList(courseId) {
  return {
    type: FETCH_HOMEWORK_LIST,
    payload: {
      promise: getHomeworkList(courseId).then((res) => res.data),
    },
  }
}

export function filterFetchBookQuestions(page, bookId) {
  return {
    type: FETCH_BOOK_QUESTIONS,
    payload: {
      promise: getBookQuestionByPage(page, bookId).then(res => res.data),
    },
  }
}

export function filterFetchHomeworkPreview(courseId) {
  return {
    type: FETCH_HOMEWORK_PREVIEW,
    payload: {
      promise: getHomeworkPreview(courseId).then((res) => res.data),
    },
  }
}

export function filterSetFilterParams(obj) {
  return {
    type: SET_FILTER_PARAMS,
    obj,
  }
}



