import React from 'react'
import PropTypes from 'prop-types'
import { Row, Rate, Button } from 'antd';
import { LiterallyCanvasReactComponent, createShape } from 'literallycanvas/lib/js'
import 'literallycanvas/lib/css/literallycanvas.css'
import queryString from 'query-string'
import { className } from '../../utils/index'
import OralCorrect from '../global/OralCorrect'
import AudioPlayer from '../global/AudioPlayer'
import s from './index.scss'

export default class CorrectPanel extends React.Component {
  constructor(props) {
    super(props)
    this.bindFunction()
    this.imageList = [] // 缩略图图片列表
    this.lc = [] // literallycanvas列表，每张图都有一个lc对象
    this.canvasDataList = []
    this.snapShot = new Map() // 每张图的快照
    this.unsubscribe = null
    this.state = {
      score: -1,
      audio: '',
      lcshapes: -1, // canvas画笔笔画
      oralstatus: 0, // 录音状态
    }
  }
  componentWillMount() {
    this.init(this.props)
  }

  componentDidMount() {
    if (window.MathJax) {
      window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub, 'root']);
    }
  }

  componentWillReceiveProps(nextProps) {
    const {
      curInx, curPicInx, curUserInx, question,
    } = this.props
    const { curInx: nextCurInx, curUserInx: nextUserInx, question: nextQuestion } = nextProps
    if (curInx !== nextCurInx && curUserInx === nextUserInx) {
      this.saveSnapshot(curInx, curPicInx)
    }
    if (curUserInx !== nextUserInx) {
      this.snapShot = new Map()
    }
    if (curInx !== nextCurInx || curUserInx !== nextUserInx || question.userId !== nextQuestion.userId) {
      this.init(nextProps)
    }
    // if (this.props.question.userId !== nextProps.question.userId) {
    //   nextProps.history.go()
    // }
  }

  componentWillUnmount() {
    this.unsubscribe()
  }

  setRecording(num) {
    this.setState({
      oralstatus: num,
    })
  }

  saveOrDelAudio(isSave, audio = '') {
    if (isSave) this.setState({ audio })
    else this.setState({ audio: '' })
  }

  bindFunction() {
    this.savePic = this.savePic.bind(this)
    this.clearPick = this.clearPick.bind(this)
    this.updateResult = this.updateResult.bind(this)
    this.saveOrDelAudio = this.saveOrDelAudio.bind(this)
    this.setRecording = this.setRecording.bind(this)
    this.gotoFilter = this.gotoFilter.bind(this)
    this.saveSnapshot = this.saveSnapshot.bind(this)
    this.haveRead = this.haveRead.bind(this)
    this.canClearPick = this.canClearPick.bind(this)
  }

  init(props) {
    const { question } = props
    this.imageList = []
    let audioTmp = ''
    if (question.mark) {
      audioTmp = question.mark.audio ? question.mark.audio : ''
    } else if (question.audio) {
      audioTmp = question.audio ? question.audio : ''
    } else {
      audioTmp = ''
    }
    this.setState({
      score: question.score,
      audio: audioTmp,
    })
    question.imgAnswers.map((item, inx) => {
      this.imageList[inx] = new Image()
      this.imageList[inx].setAttribute('crossOrigin', 'anonymous');
      this.imageList[inx].src = question.score > 0
        ? question.imgCorrect[inx]
        : question.imgAnswers[inx] // eslint-disable-line
    })
  }

  clearPick() {
    const {
      curPicInx, isHomeWork, question,
    } = this.props
    if (this.lc[curPicInx]) {
      this.lc[curPicInx].clear()
    }
    // 已阅题目，清理痕迹后状态仍为已阅
    // 已批改和为批改的题目，情况痕迹后状态为未批改
    this.setState({
      score: question.score === -3 ? -3 : (isHomeWork ? -1 : -2),
    })
  }

  // 已阅(只有测试批改有)
  haveRead(callback) {
    const { submit, question } = this.props
    const { audio } = this.state
    // "score": -1, // 成绩 -1漏做 -2未批改 -3已阅 正常评分[0-5]
    if (this.state.score !== -2) {
      return alert('只有未批改的作业才能点击已阅，你是怎么做到的？')
    }
    this.canvasDataList = this.lc.slice()
    this.imageList.map((item, inx) => {
      if (!this.lc[inx] || !this.lc[inx].getImage()) {
        // hasError = true
        this.canvasDataList[inx] = question.imgAnswers[inx]
      } else {
        this.canvasDataList[inx] = this.lc[inx].getImage().toDataURL()
      }
    })
    this.setState({
      score: -3,
    })
    submit(-3, this.canvasDataList, audio, callback)
  }

  savePic(callback) {
    const { submit, question, isHomeWork } = this.props
    const { audio } = this.state
    // "score": -1, // 成绩 -1漏做 -2未批改 -3已阅 正常评分[0-5]
    if (this.state.score === (isHomeWork ? -1 : -2)) {
      return alert('打分后才能提交')
    }
    // 测试批改在漏做情况下，不用传audio和canvasData
    if (isHomeWork || this.state.score !== -1) {
      this.canvasDataList = this.lc.slice()
      this.imageList.map((item, inx) => {
        if (!this.lc[inx] || !this.lc[inx].getImage()) {
          // hasError = true
          this.canvasDataList[inx] = question.imgAnswers[inx]
        } else {
          this.canvasDataList[inx] = this.lc[inx].getImage().toDataURL()
        }
      })
    }
    // console.log(this.canvasDataList);
    // if (!hasError) {
    submit(this.state.score, this.canvasDataList, audio, callback)
    // } else {
    //   alert('未批改完')
    // }
  }

  updateResult(value) {
    if (value === 'uncomplete') {
      return this.setState({
        score: -1,
      })
    }
    this.setState({
      score: value,
    })
  }

  // 是否可以清理痕迹
  canClearPick() {
    const { lcshapes, score } = this.state
    return score > 0 || lcshapes > 0
  }

  // 返回到filter页面
  gotoFilter() {
    const { history, isHomeWork } = this.props
    // 未批改状态下，不能返回
    if (this.state.score !== (isHomeWork ? -1 : -2)) {
      if (isHomeWork) {
        const { SetFilterParams } = this.props
        const filterParams = queryString.parse(history.location.search)
        delete filterParams.studentId
        SetFilterParams(filterParams)
      }
      history.push({ pathname: isHomeWork ? '/homework/correct/filter' : '/homework/quiz/list' })
    }
  }

  // 保存未提交的图片批改笔迹
  saveSnapshot(curInx, curPicInx) {
    // 漏做的题目，没有lc
    if (this.lc[curPicInx]) {
      this.snapShot.set(`${curInx}-${curPicInx}`, this.lc[curPicInx].getSnapshot())
    }
  }

  // 缩略图
  renderImageList() {
    const {
      question, curPicInx, changeInx, curInx,
    } = this.props
    return (
      <Row type="flex" align="middle" justify="center" style={{ marginTop: '15px' }}>
        {
          question.imgAnswers.map((item, inx) => (
            <a
              className={className(s.imgBox, {
                  [s.imgBoxActive]: curPicInx === inx,
                })}
              onClick={() => {
                this.saveSnapshot(curInx, curPicInx)
                changeInx(inx)
              }}
              key={`imgAnswer-${inx}`}
            >
              <img src={item} />
            </a>
            ))
        }
      </Row>
    )
  }

  renderCanvasList() {
    const { question, curPicInx, curInx } = this.props
    return (
      <div className={s.containerCanvas}>
        {
          question.imgAnswers.map((item, inx) => {
            if (curPicInx === inx) {
              return (
                <LiterallyCanvasReactComponent
                  key={`${question.questionId}${question.userId}-${inx}`}
                  imageURLPrefix="/static/img"
                  primaryColor="#f00"
                  secondaryColor="transparent"
                  backgroundShapes={[createShape('Image', { x: 0, y: 0, image: this.imageList[inx] })]}
                  onInit={(lc) => {
                    this.lc[inx] = lc
                    this.unsubscribe = this.lc[inx].on('drawingChange', () => {
                      this.setState({
                        lcshapes: this.lc[inx].shapes.length,
                      })
                    });
                    if (this.snapShot.get(`${curInx}-${inx}`)) {
                      lc.loadSnapshot(this.snapShot.get(`${curInx}-${inx}`))
                    }
                  }}
                />
              )
            }
            return null
          })
        }
      </div>
    )
  }

  renderAudioArea() {
    const { audio } = this.state
    if (audio && audio.length > 0) {
      return <AudioPlayer audioSrc={audio} saveOrDelAudio={this.saveOrDelAudio} />
    }
    return <OralCorrect saveOrDelAudio={this.saveOrDelAudio} setRecording={this.setRecording} />
  }

  render() {
    const { next, isHomeWork } = this.props
    const { oralstatus, lcshapes, score } = this.state

    return (
      <div className={s.wrapper}>
        {this.renderCanvasList()}
        <Row type="flex" align="middle" justify="center">
          <span>回答打分情况(低于3分为错误)：</span>
          <Rate
            value={score}
            onChange={this.updateResult}
            disabled={!isHomeWork && score === -1}
          />
        </Row>
        {this.renderImageList()}
        <Row type="flex" align="middle" justify="center" style={{ marginTop: '15px' }}>
          {(!isHomeWork && score === -1) ? null : this.renderAudioArea()}
          <Button onClick={() => this.clearPick()} disabled={!this.canClearPick()}>清空批改痕迹</Button>
          {isHomeWork ?
            null
            :
            <Button type="primary" style={{ marginLeft: '15px' }} onClick={() => this.haveRead(next)} disabled={(oralstatus !== 0 && oralstatus !== 3) || score !== -2 || lcshapes > 0}>已阅</Button>
          }
          <Button type="primary" style={{ marginLeft: '15px' }} onClick={() => this.savePic(next)} disabled={oralstatus !== 0 && oralstatus !== 3}>保存并进入下一题</Button>
          <Button type="primary" style={{ marginLeft: '15px' }} onClick={() => this.savePic(this.gotoFilter())} disabled={oralstatus !== 0 && oralstatus !== 3}>保存并退出</Button>
        </Row>
      </div>
    )
  }
}

CorrectPanel.propTypes = {
  submit: PropTypes.func.isRequired,
  next: PropTypes.func.isRequired,
  changeInx: PropTypes.func,
  curPicInx: PropTypes.number.isRequired, // 当前题目下的第几张图片
  curInx: PropTypes.number.isRequired, // 当前界面下的第几道题
  curUserInx: PropTypes.number.isRequired, // 当前url中的inx参数，比如/#/pick/quiz/4?inx=0
  question: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  SetFilterParams: PropTypes.func.isRequired,
  isHomeWork: PropTypes.bool.isRequired,
}
CorrectPanel.defaultProps = {
  changeInx: () => {},
}



