import _ from 'lodash';

import {
  GET_ALL_MENU_SUCCESS,
  UPDATE_NAVPATH,
} from '../constants';

const initialState = {
  items: [],
  navpath: [],
};

export default function menu(state = initialState, action = {}) {
  switch (action.type) {
    case GET_ALL_MENU_SUCCESS:
      return Object.assign({}, initialState, { items: action.payload.data.menus });
    case UPDATE_NAVPATH: // eslint-disable-line
      const navpath = []
      let tmpOb
      let tmpKey
      let child;
      if (Array.isArray(action.payload.data)) {
        action.payload.data.reverse().map((item) => {
          if (item.indexOf('sub') !== -1) {
            tmpKey = item.replace('sub', '');
            tmpOb = _.find(state.items, (o) => o.key == tmpKey); // eslint-disable-line
            child = tmpOb.child; // eslint-disable-line
            navpath.push({
              key: tmpOb.key,
              id: tmpOb.id,
              url: tmpOb.url,
            })
          }
          if (item.indexOf('menu') !== -1) {
            tmpKey = item.replace('menu', '');
            if (child) {
              tmpOb = _.find(child, (o) => o.key == tmpKey); // eslint-disable-line
              navpath.push({
                key: tmpOb.key,
                id: tmpOb.id,
                url: tmpOb.url,
              });
            }
          }
          return null
        })
      }
      return Object.assign({}, state, {
        currentIndex: action.payload.key * 1,
        navpath,
      });
    default:
      return state;
  }
}



