import React from 'react'
import PropTypes from 'prop-types'
import { Row, Col, Rate, Checkbox, Button } from 'antd';
import { LiterallyCanvasReactComponent, createShape } from 'literallycanvas/lib/js'
import 'literallycanvas/lib/css/literallycanvas.css'

import s from './index.css'

export default class CorrectPanel extends React.Component {
  constructor(props) {
    super(props)
    this.savePic = this.savePic.bind(this)
    this.updateResult = this.updateResult.bind(this)
    this.state = {
      score: this.props.score,
    }
  }

  componentWillMount() {
    this.image = new Image()
    this.image.setAttribute('crossOrigin', 'anonymous');
    this.image.src = this.props.imageUrl
  }

  componentDidMount() {
    if (window.MathJax) {
      window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub, 'root']);
    }
  }

  componentDidUpdate() {
    if (this.image.src !== this.props.imageUrl) {
      this.image.src = this.props.imageUrl
      this.lc && this.lc.clear() // eslint-disable-line
    }
  }

  savePic(callback) {
    const { submit } = this.props
    if (this.state.score === 0) {
      return alert('打分后才能提交')
    }
    if (this.lc.getImage()) {
      submit(this.state.score, this.lc.getImage().toDataURL(), () => {
        callback && callback()
      })
    } else {
      callback && callback()
    }
    // callback()
  }

  updateResult(value) {
    if (value === 'uncomplete') {
      return this.setState({
        score: -2,
      })
    }
    this.setState({
      score: value,
    })
  }

  render() {
    const {
      next, question, title, history, answer,
    } = this.props;
    return (
      <div className={s.wrapper}>
        <div className={s.bgMask} />
        <div className={s.panel}>
          <Row>
            <Col span={6} className={s.containerQue}>
              <div className={s.title}>原题:</div>
              <div
                dangerouslySetInnerHTML={{ __html: `${title}` }}
              />
              <div className={s.title}>解析:</div>
              {question.answer ? <div
                dangerouslySetInnerHTML={{ __html: `${question.answer}` }}
              /> : '无'}
            </Col>
            <Col span={18} className={s.canvasBox}>
              <LiterallyCanvasReactComponent
                onInit={(lc) => { this.lc = lc }}
                imageURLPrefix="/static/img"
                primaryColor="#f00"
                backgroundShapes={[createShape('Image', { x: 0, y: 0, image: this.image })]}
              />
            </Col>
          </Row>

          <Row type="flex" align="middle" justify="center">
            <span>回答打分情况(低于3分为错误)：</span>
            <Rate value={this.state.score} onChange={this.updateResult} />
            <Checkbox
              checked={this.state.score === -2}
              onChange={(value) => this.updateResult(value ? 'uncomplete' : 0)}
            >未完成
            </Checkbox>
          </Row>

          <Row type="flex" align="middle" justify="center" style={{ marginTop: '15px' }}>
            <Button onClick={() => history.goBack()}>取消</Button>
            <Button type="primary" style={{ marginLeft: '15px' }} onClick={() => this.savePic(() => history.goBack())}>保存并退出</Button>
            <Button type="primary" style={{ marginLeft: '15px' }} onClick={() => this.savePic(next)}>保存并进入下一题</Button>
          </Row>
        </div>
      </div>
    )
  }
}

CorrectPanel.propTypes = {
  back: PropTypes.func.isRequired,
  submit: PropTypes.func.isRequired,
  next: PropTypes.func.isRequired,
  score: PropTypes.number,
  imageUrl: PropTypes.string.isRequired,
}
CorrectPanel.defaultProps = {
  score: 0,
}



