import api from '../api'

import {
  GET_ALL_MENU,
  UPDATE_NAVPATH,
} from '../constants';

export function updateNavPath(path, key) {
  return {
    type: UPDATE_NAVPATH,
    payload: {
      data: path,
      key,
    },
  }
}

export function getAllMenu() {
  return {
    type: GET_ALL_MENU,
    payload: {
      promise: api.get('/menu'),
    },
  }
}



