import React from 'react'
import PropTypes from 'prop-types'
import { Card } from 'antd';

import s from './index.scss'

export default class PanelBox extends React.Component {
  render() {
    return (
      <Card className={s.container} bordered bodyStyle={this.props.bodyStyle}>
        <h3 className={s.title} dangerouslySetInnerHTML={{ __html: this.props.title }} />
        {this.props.children}
      </Card>
    )
  }
}

PanelBox.propTypes = {
  bodyStyle: PropTypes.object,
  title: PropTypes.string,
  children: PropTypes.object.isRequired,
}

PanelBox.defaultProps = {
  bodyStyle: {},
  title: '',
}



