import React from 'react';
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Layout } from 'antd';
import { withRouter } from 'react-router-dom';
import NavPath from '../../../components/global/NavPath'
import Header from '../../../components/global/Header'
import Sidebar from '../../../components/global/Sidebar'
import Footer from '../../../components/global/Footer'
import { fetchProfile } from '../../../actions/auth'
import { filterSetFilterParams } from '../../../actions/filter'
import s from './index.less';

const { Content } = Layout;

class App extends React.Component {
  constructor(props) {
    super(props);
    this.checkLogin = this.checkLogin.bind(this)
  }

  componentWillMount() {
    const { actions } = this.props;
    actions.fetchProfile();
  }

  checkLogin() {
    const { auth } = this.props
    if (auth.user === 'null') {
      window.location.href = `https://ke.youdao.com/login?back_url=${window.location.href}`
    }
  }

  render() {
    const {
      children, auth, navpath, actions, history, SetFilterParams,
    } = this.props;
    this.checkLogin()
    return (
      <Layout className={s['ant-layout']}>
        <Sidebar />
        <Layout>
          <Header profile={auth} logout={actions.logout} />
          <Content>
            <NavPath data={navpath} SetFilterParams={SetFilterParams} history={history} />
            <div style={{ minHeight: 360 }}>
              {children}
            </div>
          </Content>
          <Footer />
        </Layout>
      </Layout>
    );
  }
}

App.propTypes = {
  auth: PropTypes.object.isRequired,
  actions: PropTypes.object.isRequired,
  navpath: PropTypes.array.isRequired,
  children: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  SetFilterParams: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  const { auth, menu } = state;
  return {
    auth: auth || null,
    navpath: menu.navpath,
  };
};

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ fetchProfile }, dispatch),
    SetFilterParams: bindActionCreators(filterSetFilterParams, dispatch),
  };
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));



