import React from 'react'

import s from './index.less'

export default class Toast extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hide: false,
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        hide: true,
      })
    }, 3000)
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.txt !== nextProps.txt) {
      this.setState({
        hide: false,
      })
      setTimeout(() => {
        this.setState({
          hide: true,
        })
      }, 3000)
    }
  }

  render() {
    const { txt } = this.props
    const { hide } = this.state
    if (hide) {
      return false
    }
    return (
      <div className={s.container}>
        {txt}
      </div>
    )
  }
}



