import React from 'react'
import PropTypes from 'prop-types'
import { Breadcrumb } from 'antd'
import { Link } from 'react-router-dom'
import queryString from 'query-string'

import s from './index.less'

const defaultProps = {
  data: [],
}

const propTypes = {
  data: PropTypes.array,
  history: PropTypes.object.isRequired,
  SetFilterParams: PropTypes.func.isRequired,
}

class NavPath extends React.Component {
  render() {
    const { data, SetFilterParams, history } = this.props
    const location = { ...history.location }
    const isPickPage = location.pathname.startsWith('/pick/homework')

    const bread = data.map((item) => (
      <Breadcrumb.Item
        key={`bc-${item.key}`}
        onClick={() => {
          // 从pick页面返回到filter页面时，呈现之前filter的结果
          if (item.url === '/homework/correct/filter' && isPickPage) {
            const filterParams = queryString.parse(location.search)
            delete filterParams.studentId
            SetFilterParams(filterParams)
          } else if (item.url === '/homework') {
            SetFilterParams({})
          }
        }}
      ><Link to={item.url}>{item.id}</Link>
      </Breadcrumb.Item>
    ))
    return (
      <div className={s.container}>
        <Breadcrumb>
          <Breadcrumb.Item key="bc-0"><Link to="/">首页</Link></Breadcrumb.Item>
          {bread}
        </Breadcrumb>
      </div>
    )
  }
}

NavPath.propTypes = propTypes;
NavPath.defaultProps = defaultProps;

export default NavPath



