import {
  FETCH_QUESTION_SUCCESS,
  SUBMIT_CORRECT_SUCCESS,
  FETCH_HOMEWORK_PREVIEW_SUCCESS,
  FETCH_QUESTION_PENDING,
} from '../constants'

const initialState = {
  autoQuestions: [],
  manualQuestions: [],
};

export default function questions(state = initialState, action = {}) {
  switch (action.type) {
    case FETCH_QUESTION_PENDING:
      return {
        ...state,
        autoQuestions: [],
        manualQuestions: [],
      }
    case FETCH_QUESTION_SUCCESS:
      return {
        ...state,
        ...action.payload.data,
        code: action.payload.code,
        description: action.payload.description,
      }
    case SUBMIT_CORRECT_SUCCESS: // eslint-disable-line
      if (!action.payload.data) return state
      const temp = state.manualQuestions.slice()
      temp.splice(action.payload.inx - 1, 1, {
        ...state.manualQuestions[action.payload.inx - 1],
        score: action.payload.data.score,
        mark: {
          images: action.payload.data.mark.images,
        },
      })
      return {
        ...state,
        ...{
          manualQuestions: temp,
        },
      }
    case FETCH_HOMEWORK_PREVIEW_SUCCESS:
      return {
        ...state,
        preview: action.payload.data,
      }
    default:
      return state;
  }
}




