import {
  FETCH_PROFILE_SUCCESS,
} from '../constants';

const initialState = {
  user: null,
};

export default function auth(state = initialState, action = {}) {
  switch (action.type) {
    case FETCH_PROFILE_SUCCESS:
      return Object.assign(
        {},
        state, {
          user: action.payload.data.userId,
        },
      );
    default:
      return state;
  }
}



