import React from 'react'
import PropTypes from 'prop-types'
import { Link, Redirect } from 'react-router-dom'
import s from './index.scss'

export default class HWCHome extends React.Component {
  render() {
    const { match } = this.props
    return (
      <div className={s.test}>
        {<Redirect to="/homework/correct/filter" />}
        <h2>作业批改首页</h2>
        <Link className={s.link} to={`${match.path}filter`}>{`${match.path}filter`}</Link>
        <Link className={s.link} to={`${match.path}questions`}>{`${match.path}questions`}</Link>
      </div>
    )
  }
}

HWCHome.propTypes = {
  match: PropTypes.object.isRequired,
}



