import {
  getQuizzes,
  getQuizzesForStudent,
  postQuizzesReadAll,
} from './server'
import {
  FETCH_QUIZZES,
  FETCH_STUDENT_QUIZZES,
  FETCH_QUIZZES_READ_ALL,
} from '../constants'

export function fetchQuizzes(params) {
  return {
    type: FETCH_QUIZZES,
    payload: {
      promise: getQuizzes(params).then((res) => res.data),
    },
  }
}

export function fetchQuizzesForStudent(params) {
  return {
    type: FETCH_STUDENT_QUIZZES,
    payload: {
      promise: getQuizzesForStudent(params).then((res) => res.data),
    },
  }
}

export function fetchQuizzesReadAll(params, inx) {
  return {
    type: FETCH_QUIZZES_READ_ALL,
    payload: {
      promise: postQuizzesReadAll(params).then((res) => ({
        ...res.data,
        inx,
      })),
    },
  }
}




